
@import '../base';

.c-overlay-video{
  @include t-v-gutters;
  margin-bottom: t-to-rem(76);
  
  position: relative;
  cursor: pointer;


  &__image{
    &.is-desktop{
      display: none;

      .has-no-mobile-image &{
        display: block;
      }
    }
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__top{
    position: relative;
    margin-bottom: t-to-rem(33);
    padding-bottom: 130%;
    & > * {
      pointer-events: none;
    }
    
    &:hover{
      
      .c-overlay-video__play-button {
        transform: scale(1);
      }
    }
  }

  
  &__inner {
    @include t-absolute-center-x-y;
    width: 79.2%;
    height: 83.43%;
    z-index: 1;
    
    background-color: rgba($s-color--background, .2);
    backdrop-filter: blur(t-to-rem(32));
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
  }


  &__video-title{
    color: white;
    margin-bottom: t-to-rem(40);
    width: 80%;
  }

  &__play-button {
    @include t-width-and-height(73);
    display: block;
    margin: 0 auto;
    transform: scale(.9);
    transition: transform $s-anim--time-short $s-anim--ease-in-out;


    .icon--play{
      &-inner{
        fill: $s-color--background-tertiary;
      }
    }
  }

  &__bottom{
    text-align: center;
    @include t-gutters;

  }

  
  &__pre-title{
    color: $s-color--font-1;
    margin-bottom: t-to-rem(22);
  }
  &__heading{
    color: $s-color--font-1;
    margin: 0 auto t-to-rem(16);
    width: 90%;
  }
  &__text{
    color: $s-color--font-8;
  }

  @include t-media-query($s-small-up) {
    @include t-gutters;
    font-size: t-to-rem(54);

    &__top{
      padding-bottom: 45.3%;
    }

    &__inner{
      width: 94.3%;
      height: 89.6%;
      justify-content: center;
    }
    &__image{
      &.is-mobile{display: none;}
      &.is-desktop{display: block;}
    }
    &__pre-title{
      margin-bottom: t-to-rem(12);
    }
    &__video-title{
      font-size: t-to-rem(40);
      width: 70%;
      margin-bottom: t-to-rem(24);
    }
    &__heading{
      margin-bottom: 2%;
      padding: unset;
    }
    &__text{
      width: 35%;
      margin: 0 auto;
    }
  }


  @include t-media-query($s-medium-up) {
    margin-bottom: t-to-rem(118);
    &__play-button {
      @include t-width-and-height(80);
    }
    &__title{
      font-size: t-to-rem(48);
      width: 60%;
      margin-bottom: t-to-rem(42);
    }
    
    &__top{
      margin-bottom: t-to-rem(79);
    }
  }

    
  @include t-media-query($s-xlarge-up) {   
    &__play-button {
      @include t-width-and-height(100);
    } 
    &__title{
      margin-bottom: t-to-rem(58);
      font-size: t-to-rem(72);
    }
  }
}